import Vue from 'vue'
import Router from 'vue-router'
import Home from './views/Home.vue'

Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/',
      name: 'home',
      component: Home,
    },
    {
      path: '/about',
      name: 'about',
      component: () => import(/* webpackChunkName: "about" */ './views/About.vue')
    },
    {
      path: '/Classifydetail',
      name: 'Classifydetail',
      component: () => import(/* webpackChunkName: "about" */ './views/Classifydetail.vue')
    },
    {
      path: '/Bookdetail',
      name: 'Bookdetail',
      component: () => import(/* webpackChunkName: "Bookdetail" */ './views/Bookdetail.vue')
    },
    {
      path: '/Reader',
      name: 'Reader',
      component: () => import(/* webpackChunkName: "Reader" */ './views/Reader.vue')
    },
    {
      path: '/Searchview',
      name: 'Searchview',
      component: () => import(/* webpackChunkName: "Searchview" */ './views/Searchview.vue')
    }
  ]
})
