<template>
  <div class="searchpage">
    <div class="close" @click="closesearch">
      <i class="el-icon-close"></i>
    </div>
    <div class="searchTool">
      <el-form ref="form">
        <el-form-item>
          <el-input v-model="searchKeyWord" class="einput" @keyup.enter.native="onSubmit(searchKeyWord)"></el-input>
          <el-button type="primary" @click="onSubmit(searchKeyWord)">查询</el-button>
        </el-form-item>
      </el-form>
      <div class="subword" v-if="suggestKeyWords!=null">
        <div
          v-for="(item, index) in suggestKeyWords"
          :key="index"
          class="text item"
          @click="pushitem(item)"
        >{{item}}</div>
      </div>
    </div>

    <div class="hotbtn">
      <el-button
        round
        v-for="(item, index) in searchHotWord"
        :key="index"
        @click="searchKeyWord=item.word"
      >{{item.word}}</el-button>
    </div>
  </div>
</template>
<script>
import { setTimeout } from "timers";
export default {
  name: "Search",
  data() {
    return {
      searchKeyWord: null,
      suggestKeyWords: null,
      searchMsg: null,
      searchPic: null,
      searchHotWord: []
    };
  },
  methods: {
    pushitem(value) {
      this.searchKeyWord = value;
    },
    onSubmit() {
      this.$router.push({
        name: "Searchview",
        query: {
          Kw: this.searchKeyWord
        }
      });
      this.$store.commit("hideSearch");
    },
    closesearch() {
      this.$store.commit("hideSearch");
    }
  },
  created() {
    axios
      .get(`https://novel.juhe.im/search-hotwords`)
      .then(res => {
        this.searchHotWord = res.data.searchHotWords.slice(0, 10);
      })
      .catch(err => {
        console.error(err);
      });
  },
  watch: {
    searchKeyWord: function(newValue, oldValue) {
      let cansearch = true;
      if (cansearch == true) {
        cansearch = false;
        axios
          .get(
            `https://api.kele8.cn/agent?url=http://api.zhuishushenqi.com/book/auto-complete?query=${
              this.searchKeyWord
            }`
          )
          .then(res => {
            this.suggestKeyWords = res.data.keywords;
            setTimeout(() => {
              cansearch = true;
            }, 100);
          })
          .catch(err => {
            console.error(err);
          });
      }
      if (this.searchKeyWord == null) {
        this.suggestKeyWords = null;
      }
    }
  }
};
</script>
<style lang="less">
.searchpage {
  transition: all 0.6s;
  background: rgb(255, 255, 255);
  z-index: 100;
  width: 100vw;
  height: 100vh;
  top: -100vh;
  position: fixed;

  .el-form {
    width: 100%;
  }
  .el-form-item {
    width: 100%;
    display: flex;
    flex-wrap: nowrap;
    justify-content: space-around;
    margin: 0;
  }
  .el-form-item__content {
    display: flex;
    flex-wrap: nowrap;
    justify-content: space-around;
    input {
      width: 100%;
      flex: 1;
    }
    button {
      margin-left: 2%;
    }
  }
  .close {
    top: 0;
    left: 0;
    margin: 0.5rem;
    position: absolute;
    font-size: 2rem;
  }
  .searchTool {
    position: relative;
    margin: 2.5rem auto;
    .subword {
      width: 100%;
      top: 2.5rem;
      background: #fff;
      position: absolute;
      margin: 0.25rem auto;
      line-height: 2rem;
      .text {
        margin: 0 auto;
        width: 80%;
        background: #fdfdfd;
        padding: 0.25rem;
        border-radius: 10px;
        border-bottom: 1px solid rgba(211, 211, 211, 0.521);
      }
    }
  }
  .hotbtn {
    width: 95%;
    margin: 0 auto;
    border-bottom: 1px solid #d3d3d3;
    .el-button {
      margin: 0.5rem;
    }
  }
}
</style>
