<template>
  <div class="classify">
    <el-tabs type="border-card">
      <el-tab-pane label="男生分类">
        <el-col
          :span="8"
          v-for="(item, index) in classifyList.male"
          :key="index"
          v-on:click.native="mango('male',item.name)"
        >
          <el-card :body-style="{ padding: '0px' }">
            <img :src="cuturl+item.bookCover[0]" class="image">
            <div style="padding: 14px;">
              <span>{{item.name}}</span>
            </div>
          </el-card>
        </el-col>
      </el-tab-pane>
      <el-tab-pane label="女生分类">
        <el-col
          :span="8"
          v-for="(item, index) in classifyList.female"
          :key="index"
          v-on:click.native="mango('female',item.name)"
        >
          <el-card :body-style="{ padding: '0px' }">
            <img :src="cuturl+item.bookCover[0]" class="image">
            <div style="padding: 14px;">
              <span>{{item.name}}</span>
            </div>
          </el-card>
        </el-col>
      </el-tab-pane>
      <el-tab-pane label="漫画分类">
        <el-col
          :span="8"
          v-for="(item, index) in classifyList.picture"
          :key="index"
          v-on:click.native="mango('picture',item.name)"
        >
          <el-card :body-style="{ padding: '0px' }">
            <img :src="cuturl+item.bookCover[0]" class="image">
            <div style="padding: 14px;">
              <span>{{item.name}}</span>
            </div>
          </el-card>
        </el-col>
      </el-tab-pane>
      <el-tab-pane label="经典刊物">
        <el-col
          :span="8"
          v-for="(item, index) in classifyList.press"
          :key="index"
          v-on:click.native="mango('press',item.name)"
        >
          <el-card :body-style="{ padding: '0px' }">
            <img
              :src="cuturl+item.bookCover[Number(item.bookCover.length)  - Number(1)]"
              class="image"
            >
            <div style="padding: 14px;">
              <span>{{item.name}}</span>
            </div>
          </el-card>
        </el-col>
      </el-tab-pane>
    </el-tabs>
  </div>
</template>
<script>
export default {
  name: "classify",
  data() {
    return {
      classifyList: [],
      cuturl: "http://statics.zhuishushenqi.com"
    };
  },
  methods: {
    mango(gender, major, minor) {
      this.$router.push({
        name: "Classifydetail",
        query: {
          gender: gender,
          major: major,
          minor: minor
        }
      });
    },
    femalego(gender, major, minor) {
      console.log(gender, major, minor);
      this.$router.push({
        name: "Classifydetail",
        query: {
          gender: gender,
          major: major,
          minor: minor
        }
      });
    }
  },
  created() {
    axios
      .get(
        "https://api.kele8.cn/agent?url=http://api.zhuishushenqi.com/cats/lv2/statistics"
      )
      .then(res => {
        this.classifyList = res.data;
      })
      .catch(err => {
        console.error(err);
      });
  }
};
</script>
<style lang="less">
.classify {
  background: none;
  .el-col-8 {
    width: 31%;
    margin: 0.5rem 1%;
  }
  .el-tab-pane {
    display: flex;
    flex-wrap: wrap;
    justify-content: flex-start;
    &::after {
      content: "";
      display: block;
      clear: both;
    }
    & > div {
      align-items: flex-start;
      align-content: flex-start;
    }
    .is-always-shadow {
      height: 100%;
    }
  }
  .classify-card {
    text-align: left;
    div {
      position: relative;
      line-height: 1.5rem;
      i {
        position: relative;
        float: right;
        top: 0.25rem;
      }
      padding: 0.5rem;
      line-height: 1.5rem;
      border-bottom: 1px solid #d3d3d3;
    }
  }
}
</style>>
