<template>
  <div class="topnav">
    <i class="el-icon-arrow-left" v-on:click="goback"></i>
    <i class="el-icon-s-home" v-on:click="gohome"></i>
    <span>
      <slot name="bookname"></slot>
    </span>
    <i class="el-icon-search" @click="opensearch"></i>
    <div><slot></slot></div>
  </div>
</template>
<script>
export default {
  name: "Topnav",
  methods: {
    opensearch(){
      this.$store.commit('hideSearch')
    },
    goback() {
      this.$router.go(-1);
    },
    gohome() {
      this.$router.push('/')
    }
  }
};
</script>
<style lang="less">
.topnav {
  z-index: 10;
  font-size: 1.5rem;
  line-height: 2.25rem;
  padding: 0.25rem;
  margin-bottom: 0.5rem;
  &::after {
    content: "";
    display: block;
    clear: both;
  }
  i {
    float: left;
    display: block;
    margin: 0.5rem;
    &:nth-child(2){
      float: right;
    }
  }
  .el-icon-search{
    float: right;
  }
  span {
    font-size: 1rem;
    font-weight: 500;
    float: left;
    vertical-align: middle;
  }
}
</style>
