import Vue from 'vue'
import Vuex from 'vuex'

Vue.use(Vuex)

export default new Vuex.Store({
  state: {
    Liststate: null,
    BookChapter: null,
    Searchshow: false
  },
  mutations: {
    updateList: function (state, obj) {
      state.Liststate = obj
    },
    updateBookChapter: function (state, obj) {
      state.updateBookChapter = obj
    },
    hideSearch: function (state) {
      state.Searchshow = !state.Searchshow
    }
  },
  actions: {

  }
})
